body {
    background-color: #f8f9fa;
    font-family: Arial, sans-serif;
    text-align: center;
  }

  h1 {
    font-size: 24px;
}
  
.hero {
  background-image: url("../images/vecteezy_glossy-gaming-overlay-and-screen-border-decoration-elements_20919342.jpg");
  background-size: cover;
  height: 100vh;
  color: #fff;
  padding: 150px 0;
}
  
  .hero h1 {
    font-size: 3rem;
  }
  
  .hero p {
    font-size: 1.5rem;
  }
  
  .game-board {
    background-color: #63666a;
    padding: 50px 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .footer {
    background-color: #343a40;
    color: #fff;
    padding: 20px 0;
  }
  
   .tictactoeContainer {
    max-width: 400px;
    margin: 0 auto;
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.tictactoeBoard {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
  margin-top: 20px;
}

.cell {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  background-color: #eee;
  cursor: pointer;
}

.status {
  margin-top: 20px;
  font-size: 18px;
}

.reset-button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.reset-button:hover {
  background-color: #0056b3;
}

#online-users {
  width: 100%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #343a40;
  border: 1px solid #ccc;
  padding: 20px;
  margin: 20px 0;
}

#online-users h2 {
  color: #fff;
}

#users-list {
  list-style-type: none;
  padding: 0;
}

#users-list li {
  padding: 5px 0;
  border-bottom: 1px solid #ccc;
}

#users-list li:last-child {
  border-bottom: none;
}